/* ::::::::::::::::::::::::::::::::::::::::: BODY ::::::::::::::::::::::::::::::::::::::::: */

.banner-nov {
    background-image: url(https://images.unsplash.com/photo-1590739241856-cc8c15419fc5?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1109&q=80);
    background-position: center;
    background-size: cover;
    width: 100vw;
    height: 60vh;
}

.banner-nov{
    padding-right: 2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-align: right;
}

.banner-nov h1 {
    font-family: 'Bebas Neue', cursive;
    color: white;
    letter-spacing: 2px;
    text-shadow: 2px 2px 5px rgba(128, 128, 128, 0.743);
    padding: 0 2rem;
    font-size: 10vw;
    border-right: 5px solid white;
    font-weight: 300;
}


.container-nov {
    grid-template-columns: repeat(auto-fill, 780vw);
    display: grid;
    grid-template-columns: 300px 100px ;
    grid-template-rows: 1.4fr 0.6fr 2fr 2fr 2fr 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "content content content"
        "content content content"
        "content content content"
        "content content content"
        "content content content"
        "content content content";
}

.sidebar {
    padding: 1rem 2rem;
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 90px 250px 250px 250px 250px 250px;
    gap: 60px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "titulo titulo"
        "image1 image2"
        "image3 image4"
        "image5 image6"
        "image7 image8"
        "image9 image10";
    grid-area: sidebar;
    background-color: white;
    border-right: 3px solid grey;
    animation: scale-up-hor-left 2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@keyframes scale-up-hor-left {
    0% {
      transform: scaleX(0.4);
      transform-origin: 0% 0%;
    }
    100% {
      transform: scaleX(1);
      transform-origin: 0% 0%;
    }
  }

.titulo {
    grid-area: titulo;
}

.titulo h1{font-size: 50px;}

.image1 {
    grid-area: image1;
}

.image2 {
    grid-area: image2;
}

.image3 {
    grid-area: image3;
}

.image4 {
    grid-area: image4;
}

.image5 {
    grid-area: image5;
}

.image6 {
    grid-area: image6;
}

.image7 {
    grid-area: image7;
}

.image8 {
    grid-area: image8;
}

.image9 {
    grid-area: image9;
}

.image10 {
    grid-area: image10;
}

.image1 img, .image2 img, .image3 img, .image4 img, .image5 img, .image6 img, .image7 img, .image8 img, .image9 img, .image10 img{
    width: 90%;
    height: 280px;
    object-fit: cover;
    object-position: top;
    border-radius: 20px;
}

.content {
    justify-content: center;
    background-color: white;
    padding: 2rem 2rem;
    display: grid; 
    grid-template-columns: 450px 430px; 
    grid-template-rows: 150px 80px 380px 380px 380px 190px; 
    gap: 35px 50px; 
    grid-template-areas: 
      "text1 text1"
      "text2 text2"
      "nov1 nov2"
      "nov3 nov4"
      "nov5 nov6"
      "selection selection"; 
    grid-area: content; 
}

.text1 {
    grid-area: text1;
    font-weight: 300;
    text-align: justify;
}

.text2 {
    grid-area: text2;
    font-weight: 600;
    text-align: center;
    font-size: 20px;
    border-bottom: 5px solid #875dca;
    padding-bottom: 2rem;
    color: #3f039f;
}


.nov1 img, .nov2 img, .nov3 img, .nov4 img, .nov5 img, .nov6 img{
    filter: grayscale(100%);
    transition: filter 0.5s;
    border-radius: 10px 10px 0px 0px;
    width: 100%;
    height: 300px;
    object-fit: cover;
    object-position: center;
}

.nov1 img:hover, .nov2 img:hover, .nov3 img:hover, .nov4 img:hover, .nov5 img:hover, .nov6 img:hover{
    filter: grayscale(0);
}

.nov1, .nov2, .nov3, .nov4, .nov5, .nov6{
    border-radius: 10px;
    border: 2px solid gray;
    text-align: center;
    align-items: center;
}

.summary1, .summary2, .summary3, .summary4, .summary5, .summary6{
    font-size: 20px;
    padding: 0 2rem;
} 


.nov1 {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2.5fr 1.5fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        "."
        "summary1";
    grid-area: nov1;
}

