.selection {
    text-align: center;
    grid-area: selection;
    align-self: center;
}

.special{
    color: #875dca;
    font-weight: bold;
}

.selection .page1{color: white; font-weight: bolder; background-color: #8765be;}

.selection .pages{color: black; font-weight: 400; background-color: rgb(206, 204, 204);}

.selection a{
    text-decoration: none;
    font-size: 20px;
    padding: 1rem 1.5rem;
    border-radius: 100px;
    box-shadow: 1px 5px 2px grey;
}

body{overflow-x: hidden;}


@media only screen and (max-width : 319px){
    body{overflow-x: hidden;}
    .banner{width: 240vw; height: 80vw;}
    .container-nov {
        display: grid;
        grid-template-rows: 1.4fr 10fr 20fr 20fr 20fr 10fr;
        gap: 0px 0px;
        grid-template-columns: repeat(auto-fill, 28vw);
        grid-auto-flow: row;
        grid-template-areas:
            "content content content"
            "content content content"
            "content content content"
            "content content content"
            "content content content"
            "content content content";
    }

    .sidebar {
        grid-template-columns: repeat(auto-fill, 80vw);
        padding: 1rem 2rem;
        text-align: center;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 2px 250px 250px 250px 250px 250px;
        gap: 60px 0px;
        grid-auto-flow: row;
        grid-template-areas:
            "titulo"
            "image1"
            "image2"
            "image3"
            "image4"
            "image5"
            "image6"
            "image7"
            "image8"
            "image9"
            "image10";
        grid-area: sidebar;
        
    }

    .titulo h1{font-size: 5.5vw;}

    .content {width: 200vw;
        background-color: #F0DBD4;
        padding: 2rem 2rem;
        display: grid; 
        grid-template-columns: 1fr; 
        grid-template-rows: 1fr; 
        gap: 21px 20px; 
        grid-template-columns: repeat(auto-fill, 160vw);
        grid-template-areas: 
          "text1"
          "text1"
          "text2"
          "text2"
          "nov1"
          "nov2"
          "nov3"
          "nov4"
          "nov5"
          "nov6"
          "selection"
          "selection"; 
        grid-area: content; 
    }

    .selection{margin-bottom: 3rem;}

    .nov1, .nov2, .nov3, .nov4, .nov5, .nov6{width: 10vw;}
}


